.header {
  width: 100%;
  height: 150px;
  padding-top: 10px;
  color: rgb(209, 27, 27);
  letter-spacing: 3px;
  word-spacing: 10px;
  text-shadow: 2px 5px 5px rgba(255, 217, 0, 0.932);
  text-align: center;
  font-size: 20px;
  background-color: rgba(226, 127, 14, 0.856);
}

/* pagination */
.pagination {
  display: inline-block;
}
.pagination button {
  color: rgb(19, 8, 2);
  background-color: orange;
  font-weight: bold;
  float: left;
  padding: 8px 16px;
}

.pagination button:hover {
  background-color: rgba(64, 228, 23, 0.726);
}

/* beerList */
.item {
  border: darkgreen 3px solid;
  border-radius: 6px;
  margin: 20px;
  color: rgb(235, 142, 21);
  background-color: rgba(102, 77, 15, 0.836);
}
.item2 {
  border: darkgreen 3px solid;
  border-radius: 6px;
  margin: 20px;
  color: rgb(233, 167, 81);
  background-color: rgba(102, 77, 15, 0.836);
}
.profile {
  border: darkgreen 3px solid;
  border-radius: 6px;
  width: 300px;
  margin: 20px;
  font-size: 20px;
  color: rgb(182, 89, 35);
  background-color: rgba(21, 134, 187, 0.404);
  text-align: center;
}
.image {
  height: 50%;
  width: 50%;
  margin: 10px;
}
.list-item {
  display: grid;
  grid-template-columns: 30% 30% 30%;
  border: firebrick 3px solid;
}

/* navigation,links */
.nav-bar {
  text-align: center;
  width: 80%;
  margin-left: 10%;
  list-style-type: none;
}
.nav-item {
  padding: 5px;
  margin: 5px;
  display: inline-flex;
  border: orangered 3px solid;
  border-radius: 5px;
  text-align: center;
  font-size: 20px;
  color: darkred;
  font-weight: 500;
  text-decoration: underline;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  background-color: rgba(255, 255, 0, 0.733);
}
#button {
  float: right;
  padding: 5px;
  margin: 10px;
  border: brown 3px solid;
  border-radius: 8px;
  background-color: rgba(210, 105, 30, 0.705);
  color: rgb(49, 29, 29);
  font-weight: bold;
}

/* textbox */
.text {
  margin-left: 25%;
  width: 50%;
  border: orangered 3px solid;
  background-color: rgba(247, 247, 63, 0.61);
}

/* input,logIn,register */
.form {
  margin-left: 25%;
  padding: 10px;
  width: 50%;
  text-align: center;
  border: orangered 2px solid;
  background-color: rgba(255, 166, 0, 0.555);
}
.input {
  border: brown 2px solid;
  border-radius: 5px;
  background-color: rgba(182, 182, 52, 0.596);
  height: 25px;
}
.input:focus {
  border: rgb(136, 212, 22) 3px solid;
}
.label {
  font-size: 20px;
  color: darkred;
  font-weight: 500;
}
.button {
  padding: 5px;
  margin: 10px;
  border: brown 3px solid;
  border-radius: 8px;
  text-align: center;
  background-color: rgba(210, 105, 30, 0.705);
  color: rgb(49, 29, 29);
  font-weight: bold;
}

/* paragraphs,subheaders */
.p {
  text-align: left;
  font-size: 20px;
  color: darkred;
}
.h3 {
  font-size: 25px;
  color: rgb(102, 34, 34);
}
.h2 {
  background-color: rgba(172, 255, 47, 0.397);
  text-align: center;
}
.home {
  background-image: url("https://wallsdesk.com/wp-content/uploads/2016/10/Beer-HD-Wallpaper.jpg");
  height: 80vh;
  background-position: center;
}
.main {
  background-image: url("https://www.wallpapers4u.org/wp-content/uploads/beer_bottles_three_cold_1433_1920x1080.jpg");
  background-size: cover;
  background-position: center;
  padding-top: 10px;
}
.search {
  background-image: url("https://www.arcgis.com/sharing/rest/content/items/cbe2b3319349402c8ea1e11ebeff1185/resources/1578316937202.jpeg?w=3117");
  height: 150vh;
}

@media only screen and (max-width: 600px) {
  body {
    background-color: rgba(255, 140, 0, 0.856);
  }
  .list-item {
    display: block;
  }
  .h1 {
    margin-left: 20%;
  }
  .form {
    display: block;
  }
}
@media only screen and (max-width: 1024px) and (min-width: 600px) {
  .list-item {
    display: grid;
    grid-template-columns: 40% 40%;
    border: firebrick 3px solid;
  }
}
